import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';

const useStyles = makeStyles({
  navigaatio: {
    backgroundColor: 'white',
    alignSelf: 'stretch',
    textAlign: 'center',
    backgroundcolor: '#ededed'
  },
  otsikko : {
    fontSize: 30,
    textAlign: 'Left',
    float: 'left',
    marginTop: 7,
    color: 'green',
    textShadowColor: 'rgba(0, 0, 0, 0.75)',
    textShadowOffset: {width: -1, height: 1},
    textShadowRadius: 10
    },
});

export default function LabelBottomNavigation() {
  const classes = useStyles();
  const [value, setValue] = React.useState('recents');

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    
    <BottomNavigation value={value} onChange={handleChange} className={classes.navigaatio} style={{ background: '#ededed' }}>
      <BottomNavigationAction label="Twitter" value="twitter" icon={<TwitterIcon />} />
      <BottomNavigationAction label="Facebook" value="fb" icon={<FacebookIcon />} />
      <BottomNavigationAction label="Instagram" value="Instagram" icon={<InstagramIcon />} />
    </BottomNavigation>
  );
}