import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import { Container, Typography } from '@material-ui/core';
import mokki1 from './mokki1.jpg'
import mokki2 from './mokki2.jfif'
import mokki3 from './mokki3.jfif'
import { Link } from 'react-router-dom';

const useStyles = makeStyles({
    root: {
      maxWidth: 345,
    },
    media: {
      height: 140,
    },
  });
  
  export default function MediaCard() {
    const classes = useStyles();

    return (
            <Container>

<Card >
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image={mokki1}
          title="Otso"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            Otso
          </Typography>
          <Typography color="textSecondary" >
            Vaikka usein kuuleekin aivan järjettömiä mielipiteitä, on kuitenkin tosiasia, että organisaation uusi toimintakaavio parantaa organisaatiossa toimivien vastuunalaista asennetta.
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary">
          Lisätiedot
        </Button>
        <Button size="small" color="primary" button component={Link} to="/varaus">
          Varaa
        </Button>
      </CardActions>
    </Card>

    <Card >
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image={mokki2}
          title="Karhu"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            Karhu
          </Typography>
          <Typography  color="textSecondary" >
          Ottaen huomioon oman tilanteensa, voi vain todeta, että looginen asennoituminen asettaa tahomme tukalaan tilanteeseen ajateltaessa hiusten halkomiseksi osoittautunutta geneerisyyttä.
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary" >
          Lisätiedot
        </Button>
        <Button size="small" color="primary" button component={Link} to="/varaus">
          Varaa
        </Button>
      </CardActions>
    </Card>

    <Card >
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image={mokki3}
          title="Majava"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            Majava
          </Typography>
          <Typography color="textSecondary" >
          Vaikka osaltamme onkin saatettu syyllistyä laiminlyönteihin, voidaan taholtamme kuitenkin todeta, että yksikin kokonaisvaltaista kasvua kohti suunnattu ehdotus on kiteytetty aatteeseen, jossa pohditaan yhä enemmän meitä haittaavia taakkoja.
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary">
          Lisätiedot
        </Button>
        <Button size="small" color="primary" button component={Link} to="/varaus">
          Varaa
        </Button>
      </CardActions>
    </Card>
    


            </Container>
           );

}
