import { BrowserRouter as Router, Route } from 'react-router-dom';
import AlaValikko from './components/AlaValikko';
import VarausSivu from './components/VarausSivu';
import Aloitus from './components/Aloitus';
import Mokit from './components/Mokit';
import Info from './components/Info';
import Media from './components/media';
import Container from '@material-ui/core/Container';



function App() {

  return (
    <Container>
    <Router>  
      <AlaValikko />
      <Route path="/varaus" component={VarausSivu}/>
      <Route path="/" exact component={Aloitus} />
      <Route path="/mokit" exact component={Mokit} />
      <Route path="/info" exact component={Info} />
      <Media />
    
    </Router>
    </Container>
  );
}

export default App;
