import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import InfoIcon from '@material-ui/icons/Info';
import HomeIcon from '@material-ui/icons/Home';
import FavoriteIcon from '@material-ui/icons/Favorite';
import { Link } from 'react-router-dom';

const useStyles = makeStyles({
  navigaatio: {
    backgroundColor: 'white',
    alignSelf: 'stretch',
    textAlign: 'center',
  },
  otsikko : {
    fontSize: 30,
    textAlign: 'Left',
    float: 'left',
    marginTop: 7,
    color: 'green'
    },
});

export default function LabelBottomNavigation() {
  const classes = useStyles();
  const [value, setValue] = React.useState('recents');

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    
    <BottomNavigation value={value} onChange={handleChange} className={classes.navigaatio} style={{ background: '#ededed' }}>

      <BottomNavigationAction label="Etusivu" value="Etusivu" icon={<HomeIcon />} button component={Link} to="/" />
      <BottomNavigationAction label="Mökit" value="Mökit" icon={<FavoriteIcon />} button component={Link} to="/Mokit"  />
      <BottomNavigationAction label="Info" value="Info" icon={<InfoIcon />} button component={Link} to="/Info"  />
    </BottomNavigation>
  );
}